.item {
display: flex;
justify-content: flex-start;
align-items: center;
background-color: #fff;
padding: 10px;
box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15);
border-radius: 2px;

}

.status {
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 50px;
    background-color: red;
    margin-right: 15px;
}

.isOnline {
    background-color: green;
}

.avatar {
    display: block;
    width: 70px;
    height: 70px;
    border-radius: 5px;
    margin-right: 15px;
}

.name {
    font-size: 20px;
    font-weight: 500;
}